<template>
  <div class="flex">
    <aside>
        <Sidebar :list="list" base="complexfault" title="复杂及故障"/>
    </aside>
    <section>
        <router-view :route="route"/>
    </section>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Sidebar from '@/components/common/Sidebar';
export default {
  data () {
    return {
        list: [
            {
                txt: '钻井漏失',
                url: 'leakage'
            },
            {
                txt: '井控复杂',
                url: 'wellcontrol'
            },
            {
                txt: '井下落物',
                url: 'downhole'
            },
            {
                txt: '卡钻具/卡仪器',
                url: 'cardinstrument'
            },
            {
                txt: '井眼垮塌',
                url: 'collapse'
            },
            {
                txt: '井眼缩径',
                url: 'holeshrinkage'
            },
            {
                txt: '固井复杂',
                url: 'cementing'
            },
            {
                txt: '井控复杂',
                url: 'controlcomplexity'
            },
            {
                txt: '设备修理',
                url: 'repair'
            }
        ]
    }
  },

  components: {
      Sidebar
  },

  computed: {
      ...mapGetters({
      route: 'route'
    }),
  },

  mounted: function() {},

  methods: {}
}
</script>

<style lang='scss' scoped>
.flex {
    height: 100%;
}
aside {
    width: 260px;
}
section {
    width: calc(100% - 260px);
}
</style>



